import store from '../store/index'
import home from '../API/requestApi/home'

// 
function strToArr(str, char) { // strchar
	return str.split(char)
}

//
function previewImage(urls, index = 0) {
	let theUrls = [];
	for (let i = 0; i < urls.length; i++) {
		theUrls.push(urls[i] + '?x-oss-process=image/resize,m_lfit,h_750,w_750')
	}
	uni.previewImage({
		current: index,
		urls: theUrls
	})
}

// 
function getTopIconDistance() {
	let statusBarHeight = 0;
	let navBarHeight = 0;
	let tabBarHeight = 0;

	uni.getSystemInfo({
		success: (res) => {
			// console.log('res', res);
			// 
			statusBarHeight = res.statusBarHeight || 0;

			//  +  + 
			const menuButtonInfo = uni.getMenuButtonBoundingClientRect();
			navBarHeight = menuButtonInfo.height + (menuButtonInfo.top - statusBarHeight) * 2;
			tabBarHeight = res.screenHeight - res.safeArea.bottom;
		},
		fail: (err) => {
			// console.error(':', err);
		},
	});

	return {
		statusBarHeight: statusBarHeight, // 
		navBarHeight: navBarHeight, // 
		tabBarHeight: tabBarHeight // 
	}
}

// 
function setClipboardData(data) {
	uni.setClipboardData({
		data: data,
		success: function () {
			uni.showToast({
				title: '' + data,
				duration: 1500,
				icon: 'none'
			})
		}
	})
}

//  XX
function showDateTime(dateTime) {
	// 
	// 
	let timestamp_at = new Date(dateTime).getTime();
	// 
	let timestamp_now = new Date().getTime();

	// 
	let difference = (timestamp_now - timestamp_at) / 1000 / 60;
	// console.log(difference+'1');
	if (difference < 5) { // 1
		return '';
	} else if (difference >= 5 && difference < 60) {
		// console.log(difference+'2');
		return Math.floor(difference) + ''
	} else {
		let theYear = dateTime.substring(0, 4);
		let theMonth = dateTime.substring(5, 7);
		let theDay = dateTime.substring(8, 10);

		let now = new Date();
		let nowYear = now.getFullYear() + '';
		let nowMonth = (now.getMonth() + 1) < 10 ? '0' + (now.getMonth() + 1) : (now.getMonth() + 1) + '';
		let nowDay = now.getDate() < 10 ? '0' + now.getDate() : now.getDate() + '';
		// 
		if (theYear == nowYear && theMonth == nowMonth && theDay == nowDay) {
			// console.log(dateTime.substring(11,19));
			// console.log(theYear,theMonth,theDay);
			// console.log(nowYear,nowMonth,nowDay);
			return '' + ' ' + dateTime.substring(11, 19);
		} else {
			// console.log(dateTime.substring(11,19));
			// console.log(theYear,theMonth,theDay);
			// console.log(nowYear,nowMonth,nowDay);
			return dateTime;
		}
	}
}

//  XXXX-XX-XX_XX:XX:XX
function getNowDateTime() {
	let now = new Date();
	let nowYear = now.getFullYear() + '';
	let nowMonth = (now.getMonth() + 1) < 10 ? '0' + (now.getMonth() + 1) : (now.getMonth() + 1) + '';
	let nowDay = now.getDate() < 10 ? '0' + now.getDate() : now.getDate() + '';
	let nowHours = now.getHours() < 10 ? '0' + now.getHours() : now.getHours() + '';
	let nowMinutes = now.getMinutes() < 10 ? '0' + now.getMinutes() : now.getMinutes() + '';
	let nowSeconds = now.getSeconds() < 10 ? '0' + now.getSeconds() : now.getSeconds() + '';
	return nowYear + '-' + nowMonth + '-' + nowDay + '_' + nowHours + '_' + nowMinutes + '_' + nowSeconds
}

//  XXXX-XX-XX
function getNowDate(time) {
	let now = new Date(time);
	let nowYear = now.getFullYear() + '';
	let nowMonth = (now.getMonth() + 1) < 10 ? '0' + (now.getMonth() + 1) : (now.getMonth() + 1) + '';
	let nowDay = now.getDate() < 10 ? '0' + now.getDate() : now.getDate() + '';
	console.log(nowYear + '-' + nowMonth + '-' + nowDay)
	return nowYear + '-' + nowMonth + '-' + nowDay
}

// 
function disabled_tip(type) {
	uni.showToast({
		title: '' + type + '~',
		duration: 1500,
		icon: 'none'
	})
}

function getDateDiff(dateTimeStamp) {
	var result = '';
	var date = (new Date());
	var theDate = new Date(dateTimeStamp).getTime();
	var today = new Date(date.getFullYear(), date.getMonth(), date.getDate()).getTime(); //
	var yestday = new Date(today - 24 * 3600 * 1000).getTime();
	var minute = 1000 * 60;
	var hour = minute * 60;
	var day = hour * 24;
	var halfamonth = day * 15;
	var month = day * 30;
	var now = new Date().getTime();
	var diffValue = now - theDate;
	if (diffValue < 0) { return ''; }
	var monthC = diffValue / month;
	var weekC = diffValue / (7 * day);
	var dayC = diffValue / day;
	var hourC = diffValue / hour;
	var minC = diffValue / minute;
	if (monthC >= 1) {
		result = "" + parseInt(monthC) + "";
	}
	else if (weekC >= 1) {
		result = "" + parseInt(weekC) + "";
	}
	else if (dayC >= 1) {
		result = "" + parseInt(dayC) + "";
	}
	else if (hourC >= 1) {
		if (theDate < today && yestday <= theDate) {
			result = "";
		} else {
			result = "" + parseInt(hourC) + "";
		}

	}
	else if (minC >= 1) {
		result = "" + parseInt(minC) + "";
	} else {
		result = "";
	}
	return result;
}

// 
function upLoadImage(payload) {
	return new Promise((resolve, reject) => {
		// 
		uni.request({
			url: store.state.theUrl + '/wechat/sundry/getOssUploadSign',
			method: 'POST',
			header: {
				token: store.state.theToken
			},
			data: {
				// img()video-
				type: payload.type
			},
			success: (res) => {
				if (res.data.code == 0) {
					// 
					let imgArr = [];
					for (let i = 0; i < payload.tempFilePaths.length; i++) {
						uni.uploadFile({
							url: res.data.data.host,
							filePath: payload.tempFilePaths[i],
							name: 'file',
							formData: {
								key: res.data.data.dir + payload.name[i] + '_' + store.state.theLogonUser.id + '.' + payload.tempFiles[i].extname, // 2024-03-22_23:15:04
								policy: res.data.data.policy,
								OssAccessKeyId: res.data.data.accessid,
								success_action_status: '200',
								signature: res.data.data.signature
							},
							success: (uploadFileRes) => {
								// console.log('uploadFileRes', uploadFileRes);
								if (uploadFileRes.statusCode == 200) {
									imgArr.push({
										url: res.data.data.host + '/' + res.data.data.dir + payload.name[i] + '_' + store.state.theLogonUser.id + '.' + payload.tempFiles[i].extname,
										uuid: payload.tempFiles[i].uuid
									})

									if (imgArr.length == payload.tempFilePaths.length) {
										resolve(imgArr);
									}
								} else {
									uni.showToast({
										title: ':' + uploadFileRes.statusCode + ':' + uploadFileRes.errMsg,
										duration: 2500,
										icon: 'none'
									})
									// 
									resolve([]);
								}
							},
							fail: (res) => {
								uni.showToast({
									title: ',!',
									duration: 2500,
									icon: 'none'
								})
								// 
								resolve([]);
							}
						});
					}
				} else if (res.data.code == 500) {
					uni.showToast({
						title: '~',
						duration: 2500,
						icon: 'none'
					})
				} else if (res.data.code == 410) {
					// 
					(async function () {
						// 
						await store.dispatch('toLogon', {});

						// uni.redirectTo({
						// 	url: '/pages/push/push_zudui?type=' + payload.type
						// })
						// 
						// __that.upLoadImage();
						uni.showToast({
							title: '~',
							duration: 2500,
							icon: 'none'
						})
					})()
				} else {
					uni.showToast({
						title: res.data.msg,
						duration: 2500,
						icon: 'none'
					})
				}
			},
			fail: (res) => {
				uni.showToast({
					title: '',
					duration: 2500,
					icon: 'none'
				})
				resolve([]);
			}
		})
	})
}

function isIntoDetail(id, msg = '', showBtn = '') {
	home.getMomentInfo({
		moments_id: id,
	}).then((res) => {
		let url = "/pages/index/detail?id=" + id
		if (showBtn == 'noPhone') {
			url += "&noPhone=1"
		}
		console.log(url)
		uni.navigateTo({
			url
		});
	}).catch(async (err) => {
		if (err.code == 410) {
			await this.$store.dispatch("toLogon", {});
			this.isIntoDetail(id);
		} else if (err.code == 404 && err.msg == "") {
			uni.showToast({
				title: msg ? msg : err.msg,
				icon: "none",
			});
		}
	});
}

